<script>
	import { fly } from 'svelte/transition';
	import { quintOut } from 'svelte/easing';
	import { crossfade } from 'svelte/transition';

	const [send, receive] = crossfade({
		duration: d => Math.sqrt(d * 200),
	});

	let a = (n) => 1/n;
	let exp = '';

	const changeFunction = () => {
		reset();
		if (exp.length === 0) {
			a = (n) => 1/(n*n);
			exp = '²';
		} else {
			a = (n) => 1/(n);
			exp = '';
		}
	}

	import { Graphic, Line, PointLayer, XAxis, YAxis } from '@snlab/florence'

	let x = [1];
	let y = [1];
	let signs = [''];

	let summands = ['1/1'];
	$: allsummands = [...Array(999).keys()].map(k => `1/${k+1}${exp}`);
	let posTermPosition = 1;
	let negTermPosition = 1;

	const addPositiveTerm = () => {
		posTermPosition += 1;
		const k = 2*posTermPosition - 1;
		summands = [...summands,  `1/${k}${exp}`];
		signs = [...signs, '+'];
		x = [...x, posTermPosition + negTermPosition];
		y = [...y, y[y.length-1] + a(k)];
	}

	const addNegativeTerm = () => {
		negTermPosition += 1;
		const k = 2*negTermPosition-2;
		summands = [...summands,  `1/${k}${exp}`];
		signs = [...signs, '-'];
		x = [...x, posTermPosition + negTermPosition];
		y = [...y, y[y.length-1] - a(k)];
	}

	const addPositives = (k) => {
		for (let i = 0; i < k; i++) {
			addPositiveTerm();
		}
	}

	const addNegatives = (k) => {
		for (let i = 0; i < k; i++) {
			addNegativeTerm();
		}
	}

	const reset = () => {
		x = [1];
		y = [1];

		summands = ['1/1'];
		posTermPosition = 1;
		negTermPosition = 1;
	}

	const back10 = () => {
		summands = summands.slice(0, -10);
		x = x.slice(0, -10);
		y = y.slice(0, -10);
		posTermPosition -= signs.slice(-10).filter(x => x === '+').length;
		negTermPosition -= signs.slice(-10).filter(x => x === '-').length;
		signs = signs.slice(0, -10);
	}

	let chosen = (i, posTermPosition, negTermPosition) => {
		if (i % 2 === 0 && 2*posTermPosition > i) {
				return 'chosen-positive';
		} else if (i % 2 === 0) {
			return '';
		} else if (2*negTermPosition - 1 > i) {
			return 'chosen-negative';
		} else {
			return '';
		}
	}

</script>

<div>
	<h1>Conditional convergence of the harmonic series</h1>
</div>

<div>
	<Graphic
		width={400}
		height={200}
		scaleX={[0, 1.05*Math.max(20, x[x.length - 1])]}
		scaleY={[ Math.min(0, ...y) - 0.2, 1.05*Math.max(2, ...y)]}
		flipY
		padding={20}
	>

		{#if (x.length > 1)}
			<Line 
				{x}
				{y}
				stroke={'steelblue'}
				strokeWidth={1}
			/>
		{/if}

		{#if (x.length > 1 && x.length < 500)}
			<PointLayer 
				{x}
				{y}
				stroke={'steelblue'}
				radius={2}
				strokeWidth={1}
				fill='white'
			/>
		{/if}

		<XAxis />
		<YAxis />

	</Graphic>
</div>

<div class="button-panel">
	<button
		on:click={addPositiveTerm}>
		One more positive term
	</button>
	<button
		on:click={addNegativeTerm}>
		One more negative term
	</button>
	<button
		on:click={() => addPositives(10)}>
		10 more positive terms
	</button>
	<button
		on:click={() => addNegatives(10)}>
		10 more negative terms
	</button>
	<button
		on:click={() => addPositives(100)}>
		100 more positive terms
	</button>
	<button
		on:click={() => addNegatives(100)}>
		100 more negative terms
	</button>
	<button
		on:click={back10}>
		Back 10 terms
	</button>
	<button
		on:click={reset}>
		Reset
	</button>
	<button
		on:click={changeFunction}>
		Change Series
	</button>
</div>

<div class="flex-parent">
	<div class="flex-child">
	<div class="flex-parent pad-bottom">
		<div class="flex-child positives">
			{posTermPosition} positive term{posTermPosition > 1 ? "s" : ""}
		</div>
		<div class="flex-child negatives">
			{negTermPosition} negative term{negTermPosition > 1 ? "s" : ""}
		</div>
	</div>
	{#each allsummands as summand, i (summand)}
		<span 
			class={"summand " + chosen(i, posTermPosition, negTermPosition)}>
			{ i == 0 ? '' : (i % 2 == 1 ? '-' : '+') }
			{ summand }
		</span>
		{#if (!chosen(i, posTermPosition, negTermPosition))}
			<span 
				out:send={{key: `1/${i}`}}
				class={"summand hidden"}>
				{ i == 0 ? '' : (i % 2 == 1 ? '-' : '+') }
				{ summand }
			</span>
		{/if}
	{/each}
	</div>
	<div class="flex-child">
		<div class="total">
			{+y[y.length - 1].toFixed(3)}
		</div>
		{#each summands.slice(0, 1000) as summand, i (summand)}
			<span 
				in:receive={{key: summand}}
				out:send={{key: summand}}
				class="summand">
				{ signs[i] }
				{ summand }
			</span>
		{/each}
		{#if (summands.length > 1000)} 
			...
		{/if}
	</div>
</div>

<style>

	.hidden {
		position: absolute;
		opacity: 0;
	}

	.pad-bottom {
		padding-bottom: 20px;
	}

	.positives {
		color:rgb(204, 204, 253);
	}

	.negatives {
		color: rgb(250, 225, 112);
	}

	.total {
		font-size: 2em;
		margin-bottom: 10px;
	}

	.summand { 
		display: inline-block;
		width: 70px;
	}

	.chosen-positive {
		background-color: rgb(204, 204, 253);
		border-radius: 4px;
	}

	.chosen-negative {
		background: rgb(250, 225, 112);
		border-radius: 4px;
	}

	.flex-parent {
		display: flex;
	}

	div.flex-child {
		flex: 1;
	}
	 
	h1 {
		display: block;
		margin-left: auto;
		margin-right: auto;
	}

	div { 
		display: block;
		margin-left: auto; 
		margin-right: auto;
		text-align: center;
	}

	.button-panel {
		margin-top: 20px;
		margin-bottom: 20px;
	}
</style>